import React from "react";
import Sidebar from "react-sidebar";
import "./header.css";
import Toggle from "../images/toggle_icon.png";
import unionGapLogo from "../images/unionGapLogoDark.png";
import letterHead from "../images/letterheadInvert.png";
import facebook from "../images/facebookLink.png"
import facebookDark from "../images/facebookDark.png"
class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: false,
    };
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
    this.handlePageClick = this.handlePageClick.bind(this);
    this.gottoFacebook= this.gottoFacebook.bind(this);
  }

  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
  }
  handlePageClick(index) {
    this.onSetSidebarOpen(false);
    this.props.FlipToPage(index);
  }
  gottoFacebook()
  {
    window.open("https://www.facebook.com/Friends-of-Union-Gap-Library-and-Community-Center-112981813384787")
  }
  render() {
    return (
      <div className="header">
        <Sidebar
          sidebar={
            <div className="navButtonContainer">
              <img src={unionGapLogo} alt="logo" className="unionGapLogo" />
              <button
                className="navButton"
                onClick={() => {
                  this.handlePageClick(0);
                }}
              >
                Home
              </button>
              <button
                className="navButton"
                onClick={() => {
                  this.handlePageClick(1);
                }}
              >
                Donate
              </button>
              <button
                className="navButton"
                onClick={() => {
                  this.handlePageClick(2);
                }}
              >
                Building Plans
              </button>
              <button
                className="navButton"
                onClick={() => {
                  this.handlePageClick(3);
                }}
              >
               Events / Fundraisers
              </button>
              <button
                className="navButton"
                onClick={() => {
                  this.handlePageClick(5);
                }}
              >
               Members
              </button>
              <button
                className="navButton"
                onClick={() => {
                  this.handlePageClick(5);
                }}
              >
               Sponsors
              </button>
              <img className="fbLinkMobile" src={facebookDark} onClick={() => this.gottoFacebook()}></img>
            </div>
          }
          open={this.state.sidebarOpen}
          onSetOpen={this.onSetSidebarOpen}
          styles={{ sidebar: { background: "white", zIndex:3,} }}
        >
          <div className="navbar">
            {this.props.ScreenWidth<=800 &&   <input
              className="toggleBtn"
              type="image"
              src={Toggle}
              alt="toggle button"
              onClick={() => this.onSetSidebarOpen(true)}
            />}
          
            {this.props.ScreenWidth > 800 && (
              <div className="fullNavBar">
                  <img
                src={letterHead}
                alt="friendsoflgcc"
                className="letterHeadFullscreen"
              />
              <button
                className="fullNavButton"
                onClick={() => {
                  this.handlePageClick(0);
                }}
              >
                Home
              </button>
              <button
                className="fullNavButton"
                onClick={() => {
                  this.handlePageClick(1);
                }}
              >
                Donate
              </button>
              <button
                className="fullNavButton"
                onClick={() => {
                  this.handlePageClick(2);
                }}
              >
                Building Plans
              </button>
              <button
                className="fullNavButton"
                onClick={() => {
                  this.handlePageClick(3);
                }}
              >
               Events / Fundraisers
              </button>
              <button
                className="fullNavButton"
                onClick={() => {
                  this.handlePageClick(4);
                }}
              >
               Members
              </button>
              <button
                className="fullNavButton"
                onClick={() => {
                  this.handlePageClick(5);
                }}
              >
               Sponsors
              </button>
              <img className="fbLink" src={facebook} onClick={() => this.gottoFacebook()}/>
            
            </div>
              
            )}
             {this.props.ScreenWidth <= 800 && (
              <img
                src={letterHead}
                alt="friendsoflgcc"
                className="letterHeadMobile"
              />
            )}
          </div>
        </Sidebar>
      </div>
    );
  }
}
export default Header;
